.info-block {
  border-top: 1px solid #b8b5d6;
  padding-top: 50px;
  display: flex;
  justify-content: space-around;
  margin: 30px 0 50px;

  &__text {
    font-family: 'Cambria';
    font-weight: bold;
    font-size: 42px;
    color: #003F65;
  }

  &__img {
    width: 380px;
  }
}